<template>
    <div class="fixed inset-0 flex justify-center items-center bg-white">
      <div class="opacity-85 fixed inset-0 bg-black bg-opacity-50"></div> 
      <div class="relative z-50 bg-gray-50 opacity-85 border px-4 h-90 w-1/2 pt-5 sm:p-6 sm:pb-4">
        <div class="block justify-center gap-5 items-center"></div>
        <div class="space-y-6 container">
          <div>
            <input
              v-model="email"
              id="email"
              type="email"
              placeholder="Enter email address"
              class="w-full px-3 py-2.5 border-b bg-gray-100 placeholder:text-[#697077] border-gray-300 shadow-sm outline-none"
            />
            <input
              v-model="password_new"
              id="password"
              type="password"
              placeholder="Enter new password"
              class="w-full px-3 py-2.5 mt-1 border-b bg-gray-100 placeholder:text-[#697077] border-gray-300 shadow-sm outline-none"
            />
          </div>
          <div class="flex flex-col items-center gap-2 justify-center">
            <button
              @click="reset_password"
              class="w-full py-2 px-8 rounded-md flex bg-[#245da2] text-white items-center justify-center gap-2"
            >
              Reset Password
            </button>
            <p class="text-sm font-normal cursor-pointer text-black">
              Back to Login
            </p>
          </div>
        </div>
      </div>
    </div>
  </template>
  
  <script setup>
  import { ref } from "vue";
  import { useToast } from "vue-toastification";
  import bcrypt from 'bcryptjs';
import { createResource } from 'frappe-ui'

  const email = ref("");
  const password_new = ref("");
  const toast = useToast();
  
  const reset_password = async () => {
    try {
      if (!email.value || !password_new.value) {
        toast.error("Please fill in both email and password");
        return;
      }
      
      // Step 1: Retrieve the user by email
      const response =  createResource({
        url:"/api/method/frappe.client.get_value",
        params: {
    doctype: 'User',
     
  },
       
      });
      
      
      const result = await response.json();
      const user = result.data;
  
      if (user) {
        // Step 2: Hash the new password
        const hashedNewPassword = await bcrypt.hash(password_new.value, 12);
  
        // Step 3: Update the user's password in the database
        const updateResponse = await fetch('/api/method/frappe.client.set_value', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({
            doctype: 'User',
            name: user.name,
            fielddata: {
              password: hashedNewPassword,
            },
          }),
        });
  
        const updateResult = await updateResponse.json();
  
        if (updateResult.data) {
          toast.success("Password reset and new password set successfully");
        } else {
          throw new Error('Failed to update password');
        }
      } else {
        toast.error("User not found");
      }
    } catch (error) {
      toast.error(`An error occurred: ${error.message}`);
    }
  };
  </script>
  